import { NgModule } from '@angular/core';
//import { CommonModule } from '@angular/common';
import {RouterModule, Routes} from '@angular/router';

//import cc module cn iu hng
import { LoginComponent } from './login/login.component';
import { UserDetailComponent } from './user-detail/user-detail.component';
import { PagenotfoundComponent } from './pagenotfound/pagenotfound.component';
// import { ContactComponent } from './contact/contact.component';
import { UsersComponent } from './users/users.component';
import { HomeComponent } from './home/home.component';

const routes: Routes = [
  //khai bo route mc nh, tc l khi ko ch nh path c th th s m mn hnh ny u tin
  {path: '', redirectTo : '/login', pathMatch:'full'},
  //Khai bo cc route
  {path: 'home', component : HomeComponent},
  {path: 'login', component : LoginComponent},
  {path: 'user', component : UsersComponent},
  // {path: 'contact', component : ContactComponent},
  //Truyn vo id  xem chi tit
  {path: 'detail/:id', component : UserDetailComponent},
  //Khi nhp sai link s t chuyn vo component ny (Dng ny phi c t cui cng ca route)
  {path: '**', component : PagenotfoundComponent}
  
];

@NgModule({
  imports: [
    //CommonModule,
    RouterModule.forRoot(routes)  //Khi chy u tin khi app chng ta chy
  ],
  //Khng dng n declarations
  //declarations: []
  exports: [RouterModule]
})
export class AppRoutingModule { }
